// src/app/true-modules/admin/car-management/api/carService.ts
import { Car, ApiResponse } from '../types';

const API_BASE_URL = '/api/cars';

export const fetchCars = async (): Promise<ApiResponse<Car[]>> => {
  const response = await fetch(API_BASE_URL);
  return response.json();
};

export const createCar = async (carData: Omit<Car, 'id'>): Promise<ApiResponse<Car>> => {
  const response = await fetch(API_BASE_URL, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(carData),
  });
  return response.json();
};

export const updateCar = async (id: string, carData: Partial<Car>): Promise<ApiResponse<Car>> => {
  const response = await fetch(`${API_BASE_URL}/${id}`, {
    method: 'PUT',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(carData),
  });
  return response.json();
};

export const deleteCar = async (id: string): Promise<ApiResponse<null>> => {
  const response = await fetch(`${API_BASE_URL}/${id}`, {
    method: 'DELETE',
  });
  return response.json();
};