"use client";
import { motion } from 'framer-motion';
import { FiEdit, FiTrash2, FiEye } from 'react-icons/fi';
import Swal from 'sweetalert2';
import { Car } from '../types';

const CarTable = ({ cars, onEdit, onDelete }: {
  cars: Car[];
  onEdit: (car: Car) => void;
  onDelete: (id: string) => Promise<void>;
}) => {
  const handleDelete = async (id: string) => {
    const result = await Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!',
      showClass: { popup: 'animate__animated animate__fadeInDown' },
      hideClass: { popup: 'animate__animated animate__fadeOutUp' }
    });

    if (result.isConfirmed) {
      try {
        await onDelete(id);
        Swal.fire({
          title: 'Deleted!',
          text: 'Car has been deleted.',
          icon: 'success',
          showClass: { popup: 'animate__animated animate__bounceIn' }
        });
      } catch (error) {
        Swal.fire({
          title: 'Error!',
          text: error instanceof Error ? error.message : 'Failed to delete car',
          icon: 'error',
          showClass: { popup: 'animate__animated animate__shakeX' }
        });
      }
    }
  };

  return (
    <motion.div
      className="overflow-x-auto"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <table className="min-w-full divide-y divide-gray-200">
        {/* Table head remains exactly the same */}
        {/* ... */}

        <tbody className="bg-white divide-y divide-gray-200">
          {cars.map((car) => (
            <motion.tr 
              key={car.id} 
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.3 }}
              whileHover={{ 
                scale: 1.01, 
                boxShadow: '0 4px 6px -1px rgba(0, 0, 0, 0.1)',
                backgroundColor: 'rgba(249, 250, 251, 1)'
              }}
              className="hover:bg-gray-50"
            >
              {/* Table cells remain exactly the same */}
              {/* ... */}

              {/* Actions cell */}
              <td className="px-4 py-4 whitespace-nowrap text-right text-sm font-medium">
                <div className="flex justify-end space-x-1">
                  <motion.a 
                    whileHover={{ scale: 1.1 }}
                    whileTap={{ scale: 0.9 }}
                    href={`/tms-modules/admin/car-management/view-car/${car.id}`}
                    className="text-blue-600 hover:text-blue-900 p-1 rounded-full hover:bg-blue-50"
                    title="View details"
                  >
                    <FiEye className="h-5 w-5" />
                  </motion.a>
                  <motion.button
                    whileHover={{ scale: 1.1 }}
                    whileTap={{ scale: 0.9 }}
                    onClick={() => onEdit(car)}
                    className="text-green-600 hover:text-green-900 p-1 rounded-full hover:bg-green-50"
                    title="Edit"
                  >
                    <FiEdit className="h-5 w-5" />
                  </motion.button>
                  <motion.button
                    whileHover={{ scale: 1.1 }}
                    whileTap={{ scale: 0.9 }}
                    onClick={() => handleDelete(car.id)}
                    className="text-red-600 hover:text-red-900 p-1 rounded-full hover:bg-red-50"
                    title="Delete"
                  >
                    <FiTrash2 className="h-5 w-5" />
                  </motion.button>
                </div>
              </td>
            </motion.tr>
          ))}
        </tbody>
      </table>
    </motion.div>
  );
};

export default CarTable;