"use client";

import Swal from 'sweetalert2';
import { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { fetchCars, createCar, updateCar, deleteCar } from './api/carService';
import CarTable from './components/CarTable';
import CarForm from './components/CarForm';
import Stats from './components/Stats';
import { Car } from './types';

const ManageCars = () => {
  const [cars, setCars] = useState<Car[]>([]);
  const [loading, setLoading] = useState(true);
  const [showForm, setShowForm] = useState(false);
  const [editingCar, setEditingCar] = useState<Car | null>(null);

  useEffect(() => {
    const loadCars = async () => {
      try {
        const response = await fetchCars();
        if (response.success) {
          setCars(response.data);
        } else {
          throw new Error(response.message || 'Failed to load cars');
        }
      } catch (error) {
        Swal.fire({
          title: 'Error!',
          text: error instanceof Error ? error.message : 'Failed to load cars',
          icon: 'error',
          showClass: { popup: 'animate__animated animate__shakeX' }
        });
      } finally {
        setLoading(false);
      }
    };

    loadCars();
  }, []);

  const handleSubmit = async (carData: Car) => {
    try {
      let response;
      if (editingCar) {
        response = await updateCar(carData.id, carData);
      } else {
        response = await createCar(carData);
      }

      if (response.success) {
        setCars(prev => 
          editingCar 
            ? prev.map(c => c.id === carData.id ? response.data : c)
            : [...prev, response.data]
        );
        
        Swal.fire({
          title: 'Success!',
          text: `Car ${editingCar ? 'updated' : 'registered'} successfully`,
          icon: 'success',
          showClass: { popup: 'animate__animated animate__bounceIn' }
        });
        
        setShowForm(false);
        setEditingCar(null);
      } else {
        throw new Error(response.message || 'Operation failed');
      }
    } catch (error) {
      Swal.fire({
        title: 'Error!',
        text: error instanceof Error ? error.message : 'Operation failed',
        icon: 'error',
        showClass: { popup: 'animate__animated animate__shakeX' }
      });
    }
  };

  const handleDelete = async (id: string) => {
    try {
      const response = await deleteCar(id);
      if (response.success) {
        setCars(prev => prev.filter(car => car.id !== id));
      } else {
        throw new Error(response.message || 'Deletion failed');
      }
    } catch (error) {
      throw error; // Error is handled in CarTable component
    }
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <motion.div
          animate={{ rotate: 360 }}
          transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
          className="w-12 h-12 border-4 border-blue-500 border-t-transparent rounded-full"
        />
      </div>
    );
  }

  return (
    <div className="space-y-8">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <Stats cars={cars} />
      </motion.div>

      <div className="bg-white rounded-xl shadow-lg p-6">
        <div className="flex justify-between items-center mb-6">
          <motion.h2 
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.2 }}
            className="text-2xl font-bold text-gray-800"
          >
            Car Management
          </motion.h2>
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            onClick={() => {
              setEditingCar(null);
              setShowForm(true);
            }}
            className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
          >
            Add New Car
          </motion.button>
        </div>

        <CarTable
          cars={cars}
          onEdit={(car) => {
            setEditingCar(car);
            setShowForm(true);
          }}
          onDelete={handleDelete}
        />
      </div>

      {showForm && (
        <CarForm
          car={editingCar}
          onClose={() => {
            setShowForm(false);
            setEditingCar(null);
          }}
          onSubmit={handleSubmit}
        />
      )}
    </div>
  );
};

export default ManageCars;