"use client";
import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { FiX } from 'react-icons/fi';
import Swal from 'sweetalert2';
import { Car } from '../types';

const CarForm = ({ car, onClose, onSubmit }: {
  car: Car | null;
  onClose: () => void;
  onSubmit: (carData: Car) => Promise<void>;
}) => {
  const [formData, setFormData] = useState<Omit<Car, 'id'>>({
    plateNumber: '',
    ownerName: '',
    ownerPhone: '',
    model: '',
    carType: 'Minibus',
    manufactureYear: new Date().getFullYear().toString(),
    motorCapacity: '',
    kmPerLiter: '',
    totalKm: '',
    fuelType: 'Petrol',
    status: 'Active',
    registeredDate: new Date().toISOString().split('T')[0],
    parkingLocation: ''
  });

  const [errors, setErrors] = useState<Record<string, string>>({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    if (car) {
      setFormData({
        plateNumber: car.plateNumber,
        ownerName: car.ownerName,
        ownerPhone: car.ownerPhone,
        model: car.model,
        carType: car.carType,
        manufactureYear: car.manufactureYear,
        motorCapacity: car.motorCapacity,
        kmPerLiter: car.kmPerLiter,
        totalKm: car.totalKm,
        fuelType: car.fuelType,
        status: car.status,
        registeredDate: car.registeredDate,
        parkingLocation: car.parkingLocation
      });
    }
  }, [car]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
    if (errors[name]) {
      setErrors(prev => ({ ...prev, [name]: '' }));
    }
  };

  const validateForm = () => {
    const newErrors: Record<string, string> = {};
    if (!formData.plateNumber) newErrors.plateNumber = 'Plate number is required';
    if (!formData.ownerName) newErrors.ownerName = 'Owner name is required';
    if (!formData.ownerPhone) newErrors.ownerPhone = 'Owner phone is required';
    if (!formData.model) newErrors.model = 'Model is required';
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!validateForm()) return;

    setIsSubmitting(true);
    try {
      await onSubmit({
        ...formData,
        id: car?.id || `car-${Date.now()}`
      });
    } catch (error) {
      Swal.fire({
        title: 'Error!',
        text: error instanceof Error ? error.message : 'Failed to submit form',
        icon: 'error',
        showClass: { popup: 'animate__animated animate__shakeX' }
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4"
    >
      <motion.div
        initial={{ scale: 0.9, y: 20 }}
        animate={{ scale: 1, y: 0 }}
        exit={{ scale: 0.9, y: 20 }}
        className="bg-white rounded-xl shadow-2xl w-full max-w-2xl max-h-[90vh] overflow-y-auto"
      >
        <div className="p-6">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-2xl font-bold text-gray-800">
              {car ? 'Update Car Details' : 'Register New Car'}
            </h2>
            <motion.button
              whileHover={{ rotate: 90 }}
              whileTap={{ scale: 0.9 }}
              onClick={onClose}
              className="text-gray-500 hover:text-gray-700 p-1 rounded-full hover:bg-gray-100"
            >
              <FiX className="h-6 w-6" />
            </motion.button>
          </div>

          <form onSubmit={handleSubmit} className="space-y-6">
            {/* Rest of your form fields remain exactly the same */}
            {/* ... */}

            <div className="flex justify-end space-x-3 pt-4">
              <motion.button
                type="button"
                onClick={onClose}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50 transition-colors"
              >
                Cancel
              </motion.button>
              <motion.button
                type="submit"
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                disabled={isSubmitting}
                className={`px-4 py-2 text-white rounded-lg transition-colors ${
                  isSubmitting 
                    ? 'bg-gray-400 cursor-not-allowed' 
                    : 'bg-blue-600 hover:bg-blue-700'
                }`}
              >
                {isSubmitting ? (
                  <span className="flex items-center justify-center">
                    <motion.span
                      animate={{ rotate: 360 }}
                      transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                      className="inline-block h-4 w-4 border-2 border-white border-t-transparent rounded-full mr-2"
                    />
                    Processing...
                  </span>
                ) : car ? (
                  'Update Car'
                ) : (
                  'Register Car'
                )}
              </motion.button>
            </div>
          </form>
        </div>
      </motion.div>
    </motion.div>
  );
};

export default CarForm;